.App{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center center;
  background-image: url('./images/Coding-Challenge-Image.png');
}

.MaroonDiv {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 30%;
  z-index: -1;
  background-color: rgb(97, 24, 24);
}

.Explore {
  animation: 1s ease-out 0s slideInFromLeft;
  position: relative;
  color: white;
  margin-top: 15vw;
  margin-left: 18.5vw;
  margin-bottom: 0.1vw;
  z-index: 0;
  font-size: 10vw;
}

.Explore[fade="1"]{
  animation: wobble 1s 1;
}

.Details {
  position: relative;
  animation: 1s ease-out 0s slideInFromLeft;
  width: 50vw;
  margin-top: 0.5vw;
  margin-left: 17vw;
  display: flex;
}

.Details button {
  background: rgb(97, 24, 24);
  border-color: transparent;
  margin-left: 3vw;
}

.Details p {
  color: white;
  margin-left: 0.5vw;
}
Details img {
  max-width: 75%;
  min-width: 70%;
}

.DetailsExpand {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0vw;
  background-color: white;
  
}

.DetailsExpand[grow="1"]{
  max-height: 25vw;
  transition: height 0.7s linear;
}

.DetailsExpand h1 {
  color: rgb(97, 24, 24);
  margin: 10%;
  margin-top: 15%;
  font-size: 300%;
}

.DetailsExpand p {
  margin: 10%;
  width: 75%;
  color: rgb(145, 126, 126);
  font-size: 100%;
}
.DetailsParagraph{
  font-size: 100%;
}
.socialButtons {
  margin: 0.5vw;
  max-width: 75%;
  min-width: 70%;
}

.socialMedia {
  position: fixed;
  bottom: 0;
  right: 0;
  min-width: 1vw;
  max-width: 5vw;
  float: right;
  margin-right: 2vw;
  margin-bottom: 0.5vw;
}

@keyframes slideInFromLeft {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes fade-out{
  100% {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s infinite, opacity 2s linear;
  }
}

.close {
  position: absolute;
  right: 0.5vw;
  top: 0.7vw;
  width: 32px;
  height: 32px;
  opacity: 0.3;
}
.close:hover {
  opacity: 1;
}
.close:before, .close:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 1.5vw;
  width: 0.2vw;
  background-color: rgb(97, 24, 24)
}
.close:before {
  transform: rotate(45deg);
}
.close:after {
  transform: rotate(-45deg);
}

@keyframes wobble {
  0% { transform: translateX(0%); } 
  15% { transform: translateX(-25%) rotate(-5deg); } 
  30% { transform: translateX(20%) rotate(3deg); } 
  45% { transform: translateX(-15%) rotate(-3deg); } 
  60% { transform: translateX(10%) rotate(2deg); }
  75% { transform: translateX(-5%) rotate(-1deg); }
  100% { transform: translateX(0%); } 
} 